.container {
  display: inline-block;
  position: relative;
  background: #333333;
  border-radius: 5px; }
  .container .button {
    display: block;
    padding: 8px 10px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
    width: 100%;
    transition: background 0.25s cubic-bezier(0.42, 0, 0.58, 1); }
    @media (min-width: 600px) {
      .container .button {
        font-size: 24px; } }
    .container .button::after {
      content: '';
      width: 100%;
      height: 20px;
      background: black; }
    .container .button.white {
      background: #fff;
      color: #000000;
      filter: drop-shadow(3px 3px 1px #000000); }
      .container .button.white:hover {
        background: #A5DEE4;
        color: #fff; }
