.title-container {
  position: relative;
  margin-bottom: 10px; }
  .title-container .title {
    font-size: 28px; }
  .title-container .background {
    position: absolute;
    top: -8px;
    right: -2px;
    width: 95%;
    height: 30px;
    background: rgba(3, 174, 239, 0.3);
    transform: skew(20deg); }

.attribution {
  font-size: 12px;
  color: #666;
  margin-bottom: 20px; }

.description {
  font-size: 16px;
  line-height: 24px; }
