.container {
  white-space: nowrap;
  display: flex; }

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  padding: 0;
  border: none;
  appearance: none;
  outline: none;
  background: transparent;
  cursor: pointer;
  border-style: solid;
  border-width: 1px;
  border-radius: 8px;
  box-sizing: border-box; }
  .icon:not(:last-child) {
    margin-right: 15px; }
  .icon.borderless {
    border: none;
    margin-right: 0; }

.always-white,
.white-to-original,
.white-to-purple {
  border-color: #fff; }
  .always-white svg path,
  .white-to-original svg path,
  .white-to-purple svg path {
    fill: #fff; }

.white-to-original:hover.twitter svg path {
  fill: #1da1f2; }

.white-to-original:hover.facebook svg path {
  fill: #3B5998; }

.white-to-original:hover.youtube svg path {
  fill: #ff0000; }

.white-to-original:hover.rss svg path {
  fill: #f26522; }

.white-to-original:hover.linkedin svg path {
  fill: #0077b5; }

.white-to-original:hover.instagram svg path {
  fill: #c13584; }

.white-to-original:hover.email svg path,
.white-to-original:hover.link svg path {
  fill: #333333; }

.twitter svg {
  height: 25px; }

.facebook svg {
  height: 25px; }

.youtube svg {
  height: 10px; }

.rss svg {
  height: 12px; }

.instagram svg {
  height: 25px; }

.linkedin svg {
  height: 12px; }

.email svg {
  height: 25px; }

.link svg {
  height: 25px; }
