@keyframes gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100vh;
  padding: 60px 20px; }
  .container.main {
    background-color: #fff;
    background-size: 400%;
    animation: gradient 20s ease infinite; }
  .container.info {
    background: url("/static/city-background.png") top center/100% no-repeat;
    background-color: #080605;
    flex-direction: column;
    padding-top: 150px; }
    @media (min-width: 600px) {
      .container.info {
        padding-top: 24%; } }

.content {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 0 20px; }

.logo {
  width: 300px;
  height: 175px;
  margin-bottom: 40px; }
  @media (min-width: 600px) {
    .logo {
      width: 500px;
      height: 295px; } }

.buy-button {
  margin-bottom: 25px;
  z-index: 9999; }

.social-buttons {
  margin-top: 20px; }

.info {
  color: #fff; }
  .info .content {
    margin-bottom: 30px;
    text-align: center; }
  .info .subcontent:not(:last-child) {
    margin-bottom: 20px; }
  .info .subcontent p {
    font-size: 18px;
    line-height: 20px; }
    @media (min-width: 600px) {
      .info .subcontent p {
        font-size: 24px;
        line-height: 30px; } }
  .info .line {
    width: 300px;
    height: 2px;
    background: #A5DEE4;
    margin-bottom: 30px; }
  .info h2 {
    font-size: 36px;
    margin-bottom: 12px; }
    @media (min-width: 600px) {
      .info h2 {
        font-size: 48px; } }
  .info h3 {
    display: inline-block;
    font-size: 28px;
    margin-bottom: 12px;
    border-bottom: 2px solid #A5DEE4; }
    @media (min-width: 600px) {
      .info h3 {
        font-size: 36px; } }
  .info li {
    text-align: center;
    margin-bottom: 10px; }
    .info li .label {
      font-weight: bold;
      margin-bottom: 8px; }
  .info .description {
    font-size: 16px;
    color: #aaa;
    margin-bottom: 12px; }
    @media (min-width: 600px) {
      .info .description {
        font-size: 18px; } }
  .info .info-line {
    font-size: 18px; }
    @media (min-width: 600px) {
      .info .info-line {
        font-size: 28px; } }
  .info .label {
    padding-right: 8px; }
  .info iframe {
    margin-top: 30px;
    width: 100%; }
    @media (min-width: 600px) {
      .info iframe {
        width: 500px; } }
